
#about {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .about_container{
        display: flex;
        align-items: initial;
        justify-content: center;
        @media (max-width:993px) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
        .about__me{
            aspect-ratio: 1/1;
            border-radius: 2rem;
            background: linear-gradient(45deg, transparent, #0077B8, transparent);
            display: flex;
            align-items: center;
            justify-content: center;

            .about__me-image{
                width: 100%;
                max-width: 410px;
            }
        }

        .about__content {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            width: 50%;
            text-align:left;
            @media (max-width:993px) {
                align-items: center;
                width: 100%;
                margin-top: 32px;
            }
            .about__cards{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                @media (max-width:768px) {
                    flex-direction: column;
                }
                .about__card{
                    display: flex;
                    align-items: center;
                    margin-right: 30px;
                    justify-content: center;
                    flex-direction: column;
                    background: #0077B8;
                    border-radius: 10px;
                    padding: 50px 0;
                    border: 2px solid transparent;
                    width: 50%;
                    transition: all .4s ease;
                    &:hover {
                        border-color:#0077B8;
                        background: transparent;
                        cursor: pointer;
                    }
                    &:last-child {
                        margin: 0;
                    }
                    svg {
                        font-size: 30px;
                        margin-bottom: 10px;
                    }
                    @media (max-width:768px) {
                        flex-direction: column;
                        margin-bottom: 30px;
                        margin-right: 0;
                        width: 100%;
                        max-width: 370px;
                        padding: 25px 0;
                    }
                    h5{
                        margin-bottom: 3px;
                        font-size: 18px;
                    }
                    p{
                        font-size: 18px;
                    }
                }
            }

            .about__text{
                line-height: 1.9;
                font-size: 19px;
                margin: 32px 0;
                text-align: center;
            }
        }
    }
}