@import url('assets/fonts/stylesheet.scss');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    list-style: none;
    text-decoration: none;
}


$bgColor: #011C28;
$bgColorVariant: #2c2c6c;
$primaryColor: #4db5ff;
$primaryColorVariant: #0077B8;
$whiteColor: #fff;
$lightColor: rgba(255, 255, 255, 0.6);

$widthContainer-lg: 75%;
$widthContainer-md:86%;
$widthContainer-sm:90%;

$transition: all 400ms ease;

body{
    font-family: 'Poppins';
    background: $bgColor;
    color: $whiteColor;
    line-height: 1.7;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}


/* ===============GENERAL STYLES================= */

.container {
    width: 100%;
    max-width: 1175px;
    padding: 0 15px;
}


h1, h2, h3, h4, h5, h6 {
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section {
    margin-top: 7rem;
    padding-top: 1rem;
    height: 100%;
    & > h2 , & > h5 {
        text-align: center;
        color: $lightColor;
    }

    & > h2 {
        color: $primaryColor;
        margin-bottom: 3rem;
    }
}

.text-light {
    color: $lightColor;
    font-size: 18px;
}

a{
    color: $primaryColorVariant;
    transition: $transition;
    &:hover {
        color: $whiteColor;
    }
}

.btn {
    width: max-content;
    display: inline-block;
    color: $whiteColor;
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid $primaryColorVariant;
    &:hover {
        background: $whiteColor;
        color: $bgColor;
        border-color: transparent;
    }
}


.btn-primary {
    background: $primaryColorVariant;
    color: $whiteColor;
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

.card__onhover{
    background: $primaryColorVariant;
    border: 2px solid transparent;
    transition: $transition;
    border-radius: 10px;
    &:hover {
        background: transparent;
        border-color: $primaryColorVariant;
        transition: $transition;
    }
}


/* =========MEDIA QUERIES (MEDIUM DEVICES)====================== */

@media screen and (max-width:992px) {

    section {
        margin-top: 6rem;
    }
}



/* =========MEDIA QUERIES (SMALL DEVICES)====================== */

@media screen and (max-width:767px) {

    section > h2{
        margin-bottom: 2rem;
    }

    section {
        margin-top: 3rem;
    }
}