nav{
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 3;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    border-radius: 3rem;
    backdrop-filter: blur(15px);

    a{
        background: transparent;
        padding: 0.9rem;
        border-radius: 50%;
        display: flex;
        margin-right: 10px;
        color: rgba(255, 255, 255, 0.6);
        font-size: 1.1rem;
        &:hover {
            background: rgba(0, 0, 0, 0.2);
        }
        &.active {
            background:#0077B8;
            color: #fff;
        }
        &:last-child{
            margin: 0;
        }
        @media (max-width:350px) {
            font-size: .9rem;
            margin-right: 4px;
        }
    }
}