#experience{
    padding: 1rem 0 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .container{
        display: flex;
        align-items: center;
        justify-content: center;
        .list__stacks{
            width: 100%;
            display: grid;
            grid-template-columns: repeat(3,1fr);
            gap: 2.5rem;
            @media (max-width:993px) {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-wrap: wrap;
                gap: 0;
            }
            li{
                width: 100%;
                padding: 30px;
                min-height: 14rem;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                text-align: center;
                cursor: pointer;
                @media (max-width:993px) {
                    width: 46%;
                    margin: 12px;
                }
                @media (max-width:630px) {
                    min-height: 10rem;
                    width: 40%;
                }
                @media (max-width:580px) {
                    width: 100%;
                    margin:0 0 24px 0;
                    max-width: 290px;
                    &:last-child{
                        margin:0;
                    }
                }
                .list_stack-item{
                    span{
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        font-size: 30px;
                        font-weight: 500;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        line-height: 1.4;
                        color: #fff;
                        &:nth-child(2) {
                            color: rgba(255, 255, 255, 0.6);
                            font-size: 22px;
                        }
                        @media (max-width:993px) {
                            font-size: 27px;
                        }
                        @media (max-width:768px) {
                            font-size: 20px;
                        }
                    }
                }
                svg{
                    font-size: 40px;
                    margin-bottom: 15px;
                }
            }
        }
    }
}

