footer {
    width: 100%;
    padding: 3rem 0 8rem;
    background: #0077B8;
    text-align: center;
    margin-top: 7rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h1, a {
        color: #011C28;
    }


    .footer__logo{
        font-weight: 500;
        font-size: 50px;
        @media (max-width:480px) {
            font-size: calc(30px + 20 * ((100vw - 320px) / (480 - 320)));
        }
    }
    .container {
        margin: 30px 0;
        ul{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media (max-width:993px) {
                flex-direction: column;
            }
            li{
                @media(max-width:993px) {
                    margin-bottom: 10px;
                }
                &:last-child{
                    margin-bottom: 0;
                }
                a{
                    font-size: 20px;
                    padding: 15px 10px;
                    font-weight: 500;
                    display: flex;
                    align-items: center;
                    @media (max-width:993px) {
                        padding: 10px;
                    }
                    @media (max-width:480px) {
                        font-size: calc(15px + 4 * ((100vw - 320px) / (480 - 320)));
                    }
                }
            }
        }
    }

    .footer__socials{
        display: flex;
        align-items: center;
        justify-content: center;
        a{
            font-size: 20px;
            margin-right: 25px;
            background: #011C28;
            padding: 15px;
            color: #fff;
            border-radius: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            &:last-child{
                margin-right: 0;
            }
            &:hover {
                background: #fff;
                color: #011C28;
            }
        }
    }
}


.rights{
    margin-top: 45px;
    color: #011C28;
    font-weight: 400;
    font-size: 16px;
    @media (max-width:480px) {
        font-size: calc(12px + 2 * ((100vw - 320px) / (480 - 320)));
    }
}