#testimonials{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    .container.testimonals__container {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        max-width: 41rem;
        border-radius: 10px;
        //swiper options
        .testimonial{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            text-align: center;
            background: #0077B8;
            transition: all .4s ease;
            border: 2px solid transparent;
            padding: 1.2rem;
            border-radius: 10px;
            width: 100%;
            cursor: pointer;
            &:hover {
                transition: all .4s ease;
                background: transparent;
                border-color:rgba(77, 181, 255, 0.4);
            }
            &:last-child{
                margin-bottom: 0;
            }
            .clients__avatar{
                width: 4rem;
                aspect-ratio: 1/1;
                overflow: hidden;
                border-radius: 50%;
                margin: 0 auto 1rem;
                border: .4rem solid rgb(255, 234, 0);
            }

            h5{
                margin-bottom: 1rem;
            }
            .client__review {
                font-size: 18px;
                font-weight: 400;
            }
        }
    }
}


.swiper-pagination-bullet.swiper-pagination-bullet-active-next,
.swiper-pagination-bullet.swiper-pagination-bullet-active-prev,
.swiper-pagination-bullet.swiper-pagination-bullet-active-next-next,
.swiper-pagination-bullet.swiper-pagination-bullet-active-prev-prev {
    background: #fff;
    color: #fff;
    opacity: 1;
}

.swiper{
    position: static !important;
    padding-left:15px !important;
    padding-right: 15px !important;
    padding-bottom: 4rem !important;
    .swiper-button-prev,
    .swiper-button-next {
        transform: translate(50%,50%);
        @media (max-width:993px) {
            display: none !important;
        }
    }
    .swiper-button-prev {
        left: 18% !important;
        @media (max-width:1230px) {
            left: 8.5% !important;
        }
    }

    .swiper-button-next {
        right: 20% !important;
        @media (max-width:1230px) {
            right: 11% !important;
        }
    }
}