#services {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .services__container{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        h3{
            font-size: 50px;
            margin-bottom: 3rem;
            @media (max-width:530px) {
                font-size: calc(25px + 25 * ((100vw - 320px) / (530 - 320)));
            }
        }
        .list_offer{
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 2.5rem;
            width: 100%;
            @media (max-width:993px) {
                display: flex;
                align-items: center;
                justify-content: space-around;
                gap: 0;
                width: 100%;
            }
            @media (max-width:580px) {
                flex-direction: column;
            }
            li{
                padding: 30px;
                border-radius: 10px;
                width: 40%;
                min-height: 14rem;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                text-align: center;
                transition: .4s ease;
                cursor: pointer;
                border: 2px solid transparent;
                @media (max-width:993px) {
                    width: 44%;
                }
                &:last-child{
                    margin: 0%;
                }
                &:hover {
                    background: transparent;
                    transition: .4s ease;
                    border-color: rgba(77, 181, 255, 0.4);
                }
                @media (max-width:768px) {
                    min-height: 12rem;
                }
                @media (max-width:580px) {
                    width: 100%;
                    margin-bottom: 2rem;
                    max-width: 290px;
                }
                .offer_desc{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    span{
                        font-size: 30px;
                        font-weight: 500;
                        line-height: 1.4;
                        @media (max-width:993px) {
                            font-size: 27px;
                        }
                        @media (max-width:768px) {
                            font-size: 20px;
                        }
                    }
                }
            }

            svg{
                font-size: 40px;
                margin-bottom: 15px;
            }
        }
    }
}


