.modal_window{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    z-index: 30;
    top: 0;
    background: rgba(0, 0, 0, 0.475);
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0);
    transition: .4s ease;
    opacity: 0;
    &.active {
        transform: scale(1);
        transition: .4s ease;
        opacity: 1;
    }
    .modal__body{
        width: 100%;
        max-width: 500px;
        min-height: 20rem;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        border-radius: 15px;
        background: url('../../assets/img/completed.gif') bottom / cover no-repeat;
        h1{
            color: #011C28;
            font-size: 30px;
            margin-bottom: 30px;
            font-weight: 600;
        }
    }
}