@import url('../../assets/fonts/stylesheet.scss');
#contact {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-family: 'Poppins';
    .contact__container{
        width: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @media (max-width:787px) {
            flex-direction: column-reverse;
            align-items: center;
            justify-content: center;
        }
        .contact__options{
            width: 35%;
            @media (max-width:787px) {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
            }
            .contact__option{
                display: flex;
                font-family: 'Poppins';
                align-items: center;
                justify-content: center;
                flex-direction: column;
                padding: 20px;
                margin-bottom: 20px;
                color: #fff;
                @media(max-width:787px) {
                    width: 100%;
                    max-width: 30rem;
                }
                svg{
                    font-size: 25px;
                    margin-bottom: 3px;
                }
                h4{
                    font-size: 18px;
                    margin-bottom: 3px;
                    font-weight: 500;
                }
                h5{
                    font-size: 16px;
                    font-weight: 500;
                    margin-bottom: 8px;
                }
                p{
                    color: #b3dbf7;
                    font-size: 17px;
                    font-weight: 500;
                }
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }

        #form{
            width: 50%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            @media (max-width:787px) {
                align-items: center;
                justify-content: center;
                margin-bottom: 50px;
                width: 100%;
            }
            .input__area{
                position: relative;
                width: 100%;
                margin-bottom: 30px;
                @media (max-width:787px) {
                    max-width: 30rem;
                }
                input,
                textarea {
                    width: 100%;
                    padding: 1.5rem;
                    border-radius: 10px;
                    background: transparent;
                    resize: none;
                    border: 2px solid #0077B8;
                    outline: none;
                    font-size: 18px;
                    font-family: 'Poppins', sans-serif;
                    color: #fff;
                    font-weight: 500;
                    @media (max-width:787px) {
                        padding: 1rem;
                    }
                }

                input{
                    &:focus ~ label,
                    &:not(:placeholder-shown) ~ label {
                        top: 0;
                        transition: .4s ease;
                        font-size: 14px;
                        @media (max-width:787px) {
                            font-size: 11px;
                        }
                    }
                }


                label{
                    position: absolute;
                    top: 50%;
                    transition: .4s ease;
                    transform: translate(0, -50%);
                    left: 20px;
                    background: #011C28;
                    padding: 5px 8px;
                    font-size: 18px;
                    font-family: 'Poppins', sans-serif;
                    color: rgba(255, 255, 255, 0.79);
                    font-weight: 500;
                    cursor: text;
                    @media (max-width:787px) {
                        font-size: 15px;
                    }
                }

                &:nth-child(3){
                    label {
                        top: 25%;
                        transform: translate(0, -25%);
                        transition: .4s ease;
                        background: #011C28;
                        padding: 5px 8px;
                        cursor: text;
                    }

                    textarea{
                        position: relative;
                        font-size: 18px;
                        font-family: 'Poppins', sans-serif;
                        color: #fff;
                        font-weight: 500;
                        @media (max-width:787px) {
                            font-size: 15px;
                        }
                        &:focus ~ label,
                        &:not(:placeholder-shown) ~ label {
                            top: -7px;
                            transition: .4s ease;
                            font-size: 14px;
                            @media (max-width:787px) {
                                font-size: 11px;
                            }
                        }
                        @media (max-width:787px) {
                            padding: 1rem;
                        }
                    }
                }
            }

            .btn-primary{
                transition: .4s ease;
                font-weight: 500;
                font-size: 18px;
                &:hover {
                    transition: .4s ease;
                }
            }
        }
    }
}

/* color: rgba(255, 255, 255, 0.6);
                    font-weight: 500;
                    font-size: 18px; */



input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active  {
    -webkit-box-shadow:0 0 0 50px #011C28 inset; 
    outline-color: #0077B8 !important;
    border: 2px solid #0077B8 !important;
    -webkit-text-fill-color: #fff !important;
    font-size: 18px !important;
    font-weight: 500 !important;
    font-family: 'Poppins', sans-serif !important;
}

