#portfolio{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .portfolio__container{
        display: grid;
        grid-gap: 2rem;
        grid-template-columns: repeat(3, 1fr);
        @media (max-width:993px) {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 0;
            flex-wrap: wrap;
        }
        .portfolio__item{
            background: #0077B8;
            padding: 1.2rem;
            border-radius: 10px;
            border: 2px solid transparent;
            transition: all 400ms ease;
            cursor: pointer;
            &:last-child{
                margin: 0;
            }
            @media (max-width:993px) {
                width: 44%;
                margin: 12px;
            }
            @media (max-width:768px) {
                margin:0 0 30px 0;
                width: 100%;
                max-width: 400px;
            }
            &:hover img{
                transform: scale(1.1);
                transition: .4s ease;
            }

            &:hover {
                border-color: rgba(77, 181, 255, 0.4);
                background: transparent;
            }
            .portfolio__item-image{
                overflow: hidden;
                border-radius: 10px;
                img{
                    border-radius: 10px;
                    transition: .4s ease;

                }
            }
            h3{
                margin: 1.5rem 0;
            }
            .btn__live{
                margin-left: 1.5rem;
                background: #fff;
                color: #000;
                &:hover {
                    color: #fff;
                    background: #011C28;
                    border-color: #71cdff;
                }
            }

            .link_to_git{
                border-color: #71cdff;
                color: #fff;
                &:hover {
                    background: #0077B8;
                    border-color: transparent;
                    color: #fff;
                }
            }
        }
    }
}