
header {
    height: 100%;
    overflow: hidden;
    padding-top: calc(35px + 70 * ((100vw - 320px) / (1440 - 320)));
    display: flex;
    align-items: center;
    justify-content: center;
    .header__container{
        height: 100%;
        text-align: center;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        /* =========CTA========== */
        .cta{
            margin-top: 2.5rem;
            display: flex;
            justify-content: center;
            a{
                &:nth-child(1) {
                    margin-right: 1.2rem;
                }
            }
        }
        /* =========HEADER SOCIALS========== */
        .header__socials{
            display: flex;
            flex-direction: column-reverse;
            align-items: center;
            gap: 0.8rem;
            position: absolute;
            left: 15px;
            bottom: 0;
            @media (max-width:993px) {
                display: none;
            }
            &::before {
                content: '';
                width: 1px;
                height: 2rem;
                background: #4db5ff;
            }
            a{
                font-size: calc(14px + 11 * ((110vw - 320px) / (1440 - 320)));
                margin-bottom: 10px;
                &:first-child{
                    margin: 0;
                }
            }
        }

        /* =========HEADER SOCIALS========== */
        .me {
            background: linear-gradient(#0077B8, transparent);
            /* width: 22rem;
            height: 30rem;
            position: absolute;
            left: calc(50% - 11rem); */
            margin-top: 4rem;
            border-radius: 30rem 30rem 0 0;
            padding: 2rem 1.5rem 1.5rem 1.5rem;
            @media (max-width:993px) {
                max-width: 500px;
                width: 100%;
            }
        }

        /* =========SCROLL-DOWN========== */
        .scroll__down{
            position: absolute;
            right: 28px;
            width: fit-content;
            height: fit-content;
            transform-origin: right;
            bottom: -19px;
            font-weight: 300;
            transform: rotate(90deg);
            font-size: calc(14px + 11 * ((110vw - 320px) / (1440 - 320)));
            @media (max-width:993px) {
                display: none;
            }
        }
    }
}


